.login-form {
  width: 300px;
}
.login-form-register {
  color: #fff;
  margin-top: 10px;
}
.login-form-register a {
  border-bottom: 1px solid #fff;
  color: #1890ff;
}
.login-form-userName {
  width: 350px;
  margin: 30px 0 ;
}
.login-form-valid {
  width: 230px;
  float: left;
}
.login-form-valid-countdown {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 112px;
}
.login-form-valid-input {
  float: left;
  width: 220px;
}
.login-form-valid-button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  width: 112px;
}
.login-contain {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #f1efef;
  width: 500px;
  height: 350px;
  margin-top: 100px;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}
.login-contain h1 {
  margin-bottom: 2rem;
}
.login-form-forgot {
  float: right;
}
.login-form-button {
  width: 100%;
  border-color: #fff;
  border-style: 1px;
}
.page {
  display: flex;
  justify-content: center;
  position: relative;
  height: 100vh;
  background: #f4f5f5;
}
.goods__action-bar-btn {
  margin: 0 5px;
}
