.approveProjectList {
  width: 100%;
  padding: 20px;
  height: calc(100vh - 86px);
}
.approveProjectList-table {
  overflow: scroll;
  height: calc(100vh - 118px);
}
.approveProjectList-form {
  height: 100%;
  overflow: auto;
}
.approveProjectList-form .ant-form-item-label {
  text-align: right !important;
  white-space: pre-wrap !important;
  line-height: 22px !important;
}
.approveProjectList-form .ant-form-item-control {
  line-height: 22px !important;
}
.approveProjectList-form-contain {
  width: 100%;
}
.approveProjectList-form-contain-info {
  width: 50%;
  float: left;
  margin-left: 15%;
}
.approveProjectList-form h1 {
  text-align: center;
}
.approveProjectList-form h3 {
  text-align: center;
}
