.editNotice {
  height: calc(100vh - 86px);
  width: 100%;
  padding: 20px;
  overflow: hidden;
}
.editNotice-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.editNotice-form-item {
  width: 600px;
}
.editNotice-form-item-textarea {
  height: 200px!important;
}
.editNotice-form-item .ant-form-item-label {
  text-align: left;
}
.editNotice-form-btns {
  width: 600px;
  display: flex;
  justify-content: space-between;
}
.editNotice-add {
  margin-bottom: 10px;
}
.editNotice-table {
  overflow: scroll;
  height: calc(100vh - 140px);
}
