.approveList {
  width: 100%;
  padding: 20px;
  height: calc(100vh - 86px);
}
.approveList-table {
  overflow: scroll;
  height: calc(100vh - 118px);
}
.approveList-form {
  height: 100%;
  overflow: auto;
}
.approveList-btns {
  position: absolute;
  bottom: 30px;
  z-index: 99;
}
.approveList-btn {
  margin: 10px;
}
.approveList h1 {
  text-align: center;
}
.approveList h3 {
  text-align: center;
}
