.applyDataBase {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 86px);
}
.applyDataBase-btns {
  display: flex;
  align-items: center;
}
.applyDataBase-btn {
  margin: 0 30px;
  width: 100px;
  height: 35px;
}
.applyDataBase-photoBtn {
  border-bottom: 1px solid #1890ff;
  position: absolute;
  right: 30px;
}
.applyDataBase-form-save {
  position: fixed;
  bottom: 20px;
  left: 210px;
}
.applyDataBase-form-submit {
  position: fixed;
  bottom: 20px;
  left: 280px;
}
.applyDataBase-form-print {
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 99;
}
.applyDataBase-content {
  width: 100%;
  display: flex;
  overflow: auto;
  justify-content: center;
  height: calc(100vh - 130px);
  padding: 48px;
}
.applyDataBase-content-list {
  width: 100%;
}
.applyDataBase-content-list-word {
  margin: 0 50px 0 0;
}
.applyDataBase-content .ant-form-item-label {
  text-align: left !important;
}
.applyDataBase-content .ant-form-item {
  margin-bottom: 8px;
}
