.applyProject-content {
  width: 100%;
  display: flex;
  overflow: auto;
  justify-content: center;
  height: calc(100vh - 130px);
  padding: 48px;
}
.applyProject-content-list {
  width: 100%;
}
.applyProject-content-list-word {
  margin: 0 50px 0 0;
}
.applyProject-form {
  width: 100%;
}
.applyProject-form .ant-form-item-label {
  text-align: right !important;
  white-space: pre-wrap !important;
  line-height: 32px !important;
  font-weight: bold;
}
.applyProject-form .ant-form-item-control {
  line-height: 32px !important;
}
.applyProject-form-textarea {
  height: 120px !important;
}
.applyProject-form h1 {
  text-align: center;
}
.applyProject-form h3 {
  text-align: center;
}
.applyProject-form-save {
  position: fixed;
  bottom: 20px;
  left: 210px;
}
.applyProject-form-submit {
  position: fixed;
  bottom: 20px;
  left: 280px;
}
.applyProject-form-print {
  position: fixed;
  bottom: 20px;
  right: 30px;
}
.applyProject-form-contain {
  width: 100%;
}
.applyProject-form-contain-info {
  width: 50%;
  float: left;
  margin-left: 5%;
}
.applyProject-form-contain-notice {
  height: 100%;
  border-left: 1px solid #e8e8e8;
  margin: 0;
  padding: 0 20px;
  width: 340px;
  float: left;
}
.applyProject-form-contain-notice span {
  width: 100%;
  text-align: center;
  display: block;
  font-size: 18px;
  font-weight: bold;
}
.applyProject-form-contain-notice .ant-alert-message {
  margin-bottom: 0px!important;
  width: 100%;
  text-align: left;
  display: block;
  font-size: 12px;
  font-weight: normal;
}
.applyProject-form-contain-notice-alerts {
  margin: 16px 0 ;
}
.applyProject-form-contain-notice-alert {
  margin: 5px 0 ;
}
.applyProject-form-contain-notice li {
  margin: 5px 0;
}
