.home {
  width: 100%;
  height: 100vh;
  display: flex;
}
.home-modal {
  width: 500px;
}
.home-left {
  width: 182px;
  height: 100vh;
}
.home-left-logo {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-left-logo-img {
  width: 160px;
  height: 70px;
}
.home-right {
  width: calc(100vw - 182px);
  height: 100vh;
}
.home-right-top {
  display: flex;
  width: 100%;
  height: 100vh;
}
.home-right-top-user {
  width: 200px;
  height: 70px;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  justify-content: center;
  align-items: center;
}
.home-right-top-user span {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home-right-top-tabs {
  width: 100%;
  height: 100vh;
}
